export default{
	data(){
		return{
			passitive:'',
			mobile:'',
			pwd:'',
			newpwd:'',
			invited_code:'',
			verifycode:'',
			reg:/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
			validateMsg:'',
			// 倒计时
			remain:60,
			timer:null,
			enable:true
		}
	},
	onLoad(e){
		this.mobile=e.mobile
		this.passitive=e.passitive
	},
	methods:{
		loginpsd(){
			uni.redirectTo({
				url:'/pages/login/loginPsd?passitive='+this.passitive
			})
		},
		submit(){
			var that=this
			var vali=this.validate()
			if(vali===false){
				this.$msg(this.validateMsg)
				setTimeout(function(){
					that.validateMsg=''
				},1000)
				return false;
			}
			this.$loading()
			var params={
				mobile:this.mobile,
				// pwd:this.pwd
			}
			// var url='&r=api.member.account.register'
			var url=`&r=api.member.account.register&mobile=${this.mobile}&pwd=${this.pwd}&verifycode=${this.verifycode}&invited_code=${this.invited_code}`
			  this.$apiPost(url).then((res) =>{
				  uni.hideLoading()
				  setTimeout(function(){
					that.$msg('注册成功')  
				  },100)
				  uni.setStorageSync('userInfo',res.data)
				 if(that.passitive){
				 						    uni.$emit('logined')
				 							uni.navigateBack({
				 								delta:2
				 							})
				 }else{
				 						   uni.switchTab({
				 						   	url:'../tabBar/mine'
				 						   })
				 }
			  })
		},
		validate(){
			if(!this.reg.test(this.pwd)){
				this.validateMsg='密码需为8-16位数字字母组合'
				return false;
			}
			if(this.pwd!=this.newpwd){
				this.validateMsg='两次输入密码不一致'
				return false;
			}
		},
		getveri(){
		if(this.enable){
		  var that=this
		  this.enable=false
		  // this.getcode()
		  this.timer = setInterval(function(){
		        // 定时器到底了 兄弟们回家啦
		        that.settime()
		      }, 1000)
			}
		},
		  settime(){
		    if(this.remain == 1){
		      this.remain=60
		      clearInterval(this.timer);
		          this.enable=true
		    }else{
		      this.remain--;
		    }
		  },
		  getcode(){
		    var that=this,param
		    http.post('/sendsms',param).then(res =>{
		      that.$success('发送成功')
		    }).catch((reason) =>{
		        that.$error(reason)
		    })
		  }
	}
}
